<template>
	<div class="typewriter-text">{{text}}</div><div class="typewriter-cursor" ref="cursor"></div>
</template>
<script type="text/javascript">
	export default {
		data(){
			return {
				_PART: 0,
				_PART_INDEX: 0,
				_INTERVAL_VAL: null,
				_CONTENT: [
					"Programmer", 
					"Back-end Developer", 
					"Front-end Developer", 
					"Freelancer"
				],
				text: ''
			}
		},
		mounted(){
			this._INTERVAL_VAL = setInterval(this.TypeInit, 100);
		},
		methods: {
			TypeInit(){
				var mod = this
				var text =  this._CONTENT[this._PART].substring(0, this._PART_INDEX + 1);
				this.text = text;
				this._PART_INDEX++;
				if(text === this._CONTENT[this._PART]) {
					this.$refs.cursor.style.display = 'none';
					clearInterval(this._INTERVAL_VAL);
					setTimeout(function() {
						mod._INTERVAL_VAL = setInterval(mod.TypeDelete, 50);
					}, 1000);
				}
			},

			TypeDelete(){
				var mod = this
				var text =  this._CONTENT[this._PART].substring(0, this._PART_INDEX - 1);
				this.text = text;
				this._PART_INDEX--;
				if(text === '') {
					clearInterval(this._INTERVAL_VAL);
					if(this._PART == (this._CONTENT.length - 1))
						this._PART = 0;
					else
						this._PART++;
					this._PART_INDEX = 0;
					setTimeout(function() {
						mod.$refs.cursor.style.display = 'inline-block';
						mod._INTERVAL_VAL = setInterval(mod.TypeInit, 100);
					}, 200);
				}
			}
		}
	}
</script>